*
{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 25px;
  letter-spacing: 0.2ch;
}

body
{
  padding: 1em;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 1rem;
}

:root {
  --background: #0d1117;
  --foreground: #161b22;
  --text-primary: #c9d1d9;
  --text-secondary: #8b949e;
  --border: #30363d;
  --link: #58a6ff;
  --button-bg: #21262d;
  --button-hover: #30363d;
  --input-bg: #0d1117;
  --input-border: #30363d;
  --card-bg: #161b22;
  --card-hover: #21262d;
}

body {
  background-color: var(--background);
  color: var(--text-primary);
  font-family: Arial, sans-serif;
  height: 100vh;
}

.container
{
  width: 90%;
  margin: 4rem auto;
  padding: 3rem 0.9rem;
  border: 1px solid var(--border);
}

h1 {
  font-size: 3rem;
  font-weight: 600;
  color: #c9d1d9;
  margin-bottom: 16px;
}

input {
  background-color: #0d1117;
  width: 100%;
  border: 1px solid #30363d;
  color: #c9d1d9;
  padding: 15px 10px;
  margin-top: 1rem;
  border-radius: 6px;
  outline: none;
  font-size: 14px;
  transition: border 0.2s ease-in-out;
}

input:focus {
  border-color: #58a6ff;
}

button {
  background-color: var(--button-bg);
  color: var(--text-primary);
  padding: 8px 16px;
  border: 1px solid var(--border);
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

button:hover {
  background-color: var(--button-hover);
}

.btn-danger {
  background-color: #dc3545; /* Vermelho */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-danger:hover {
  background-color: #c82333; /* Vermelho mais escuro no hover */
}

.btn-success {
  background-color: #28a745; /* Verde */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-success:hover {
  background-color: #218838; /* Verde mais escuro no hover */
}


a {
  color: var(--link);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.card 
{
  padding: 1em 0.5em;
  margin: 1em auto;
  border: 1px solid #30363d;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1em;
  background-color: var(--button-bg);
}

.card p, p b 
{
  font-size: 18px;
}

.cabecalho
{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 600px)
{
  input
  {
    width: 80%;
    padding: 15px;
  }
}

@media (min-width: 750px)
{
  .app
  {
    width: 80%;
    margin: 0 auto;
  }

  .card
  {
    width: 50%;
  }

  
  input
  {
    width: 70%;
  }
}

@media (min-width: 900px)
{
  input
  {
    width: 40%;
  }
}